@import '../../variables.scss';

.chat {
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  display: flex;

  &__menu-container {
    width: 350px;
    flex-shrink: 0;
    background: rgba(255, 255, 255, 0.7);
  }

  &__menu {
    width: 100%;
    display: flex;
    justify-content: space-around;
    position: relative;

    &::after {
      content: '';
      display: block;
      width: 25%;
      height: 2px;
      background-color: #397387;
      position: absolute;
      bottom: 0;
    }
    &--active-0 {
      &::after {
        left: 0%;
      }
    }
    &--active-1 {
      &::after {
        left: 25%;
      }
    }
    &--active-2 {
      &::after {
        left: 50%;
      }
    }
    &--active-3 {
      &::after {
        left: 75%;
      }
    }
  }

  &__icon {
    width: 50px;
  }

  &__container {
    width: 100%;
  }

  &__main {
    padding: 0 5px;
  }
}
